<template>
  <div class="row mt-0 d-flex align-items-start" id="login-page">
    <div class="col-12 flex-center">
      <div class="flex-center login-box box flex-column">
        <h2>Reset password</h2>
        <form @submit.prevent="onSubmit" class="w-100">
          <label for="Password" class="custom-label">New Password</label>
          <el-input
            name="Password"
            class="w-100"
            type="password"
            placeholder="Enter your new password"
            v-model="password"
          />
          <el-button
            :loading="isLoading"
            class="w-100 mx-0 mb-3"
            type="primary"
            native-type="primary"
            >Save</el-button
          >
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      email: "",
      password: "",
      isLoading: false,
      timestamp: "",
      token: "",
      uid: "",
    };
  },
  created() {
    this.uid = this.$route.query.uid;
    this.token = this.$route.query.token;
  },
  methods: {
    onSubmit() {
      this.isLoading = true;
      this.$axios
        .put("reset-password/", {
          password: this.password,
          token: this.token,
          uid: this.uid,
        })
        .then((res) => {
          this.isLoading = false;
          this.$noti.display({
            color: "success",
            title: "Success",
            message: res.data.details,
          });
          this.$router.push("/");
        })
        .catch((err) => {
          this.isLoading = false;
          this.$noti.display({ message: err, status: "error", color: "error" });
        });
    },
  },
};
</script>